import logo from './logo.svg';
import './App.css';
import React from 'react';
import {bankOne, bankTwo} from './banks'

const activeStyle = {
   backgroundColor: "orange",
   boxShadow: "inset 5px 5px 10px #000000, inset -5px -5px 10px blue",
   margin: 5,
  
}
const inactiveStyle = {
  backgroundColor: "gray",
  margin: 5,
  
}

const powerOff = {
  backgroundColor: "rgba(100,100,100,0.8)"
}
const powerOn = {
  background: "linear-gradient(270deg, rgb(14, 202, 161) 0%, rgb(179, 184, 118) 44%, rgba(95, 219, 45) 100%)"
}


class DrumPad extends React.Component {
  constructor(props){
    super(props);
    this.state = {
    padStyle: inactiveStyle

  }
    this.playSound = this.playSound.bind(this);
    this.handlePad = this.handlePad.bind(this);
    this.handleKeyPress = this.handleKeyPress.bind(this);
    
}
componentDidMount() {
  document.addEventListener('keydown', this.handleKeyPress);
}
componentWillUnmount() {
  document.removeEventListener('keydown', this.handleKeyPress);
}
handleKeyPress(e) {
  if (e.keyCode === this.props.keyCode) {
    this.playSound();
  }
}

handlePad(){
  if (this.props.power) {
    if (this.state.padStyle.backgroundColor === 'orange') {
      this.setState({
        padStyle: inactiveStyle
      });
    } else {
      this.setState({
        padStyle: activeStyle
      });
    }
} 
}
playSound() {
  const sound = document.getElementById(this.props.keyTrigger);
  sound.currentTime = 0;
  sound.play();
  this.handlePad();
  setTimeout(() => this.handlePad(), 100)
  this.props.updateDisplay(this.props.clipId.replace(/-/g," "))
}
render(){
return (
  <div
          className='drum-pad'
          id={this.props.clipId}
          onClick={this.playSound}
          style={this.state.padStyle}
  >
<audio
          className='clip'
          id={this.props.keyTrigger}
          src={this.props.clip}
></audio>
  {this.props.keyTrigger}
    </div>
)
}
}
class MusicPad extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      active : true,
    }
    
  }
  
 
  render (){
    let thePad;
    if (this.props.power) {
      thePad = this.props.currentBank.map((drumObj, i, padBankArr) => {
        return (
          <DrumPad
            clip={padBankArr[i].url}
            clipId={padBankArr[i].id}
            keyCode={padBankArr[i].keyCode}
            keyTrigger={padBankArr[i].keyTrigger}
            power={this.props.power}
            updateDisplay={this.props.updateDisplay}
          />
        );
      });
    } else {
      thePad = this.props.currentBank.map((drumObj, i, padBankArr) => {
        return (
          <DrumPad
            clip='#'
            clipId={padBankArr[i].id}
            keyCode={padBankArr[i].keyCode}
            keyTrigger={padBankArr[i].keyTrigger}
            power={this.props.power}
            updateDisplay={this.props.updateDisplay}
          />
        );
      });
    }
    return <div className='pad-bank'>{thePad}</div>;
  }
}
class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      power: true,
      currentBank: bankOne,
      currentDisplay: 'Push A Button',
      currentBankDisplay: 'Bank 1',
      theVolume: '0.5',
      start: powerOn,
      start2: {}
    }
      this.displayClipName = this.displayClipName.bind(this);
      this.selectBank = this.selectBank.bind(this);
      this.adjustVolume = this.adjustVolume.bind(this);
      this.powerControl = this.powerControl.bind(this);
      this.clearDisplay = this.clearDisplay.bind(this);

  }
  powerControl() {
    this.setState({
      power: !this.state.power
    });
    if (!this.state.power) {
      this.setState({
        start:powerOn
      })
    } else if (this.state.power) {
      this.setState({
        start:powerOff,
      })
    }
    
  }
  selectBank() {
    if (this.state.power) {
      if (this.state.currentBank == bankOne) {
        this.setState({
          currentBank: bankTwo,
          currentBankDisplay: 'Bank 2'
        });
      } else {
        this.setState({
          currentBank: bankOne,
          currentBankDisplay: 'Bank 1'
        });
      }
    }
  }
  displayClipName(name) {
    if (this.state.power) {
      this.setState({
        currentDisplay: name
      });
    }
  }
  adjustVolume(e) {
    if (this.state.power) {
      this.setState({
        theVolume: e.target.value,
        currentDisplay: 'Volume: ' + Math.round(e.target.value * 100)
      });
      setTimeout(() => this.clearDisplay(), 1000);
    }
  }
  clearDisplay() {
    this.setState({
      currentDisplay: String.fromCharCode(160)
    });
  }
  render (){
    const clips = [].slice.call(document.getElementsByClassName('clip'));
      clips.forEach(sound => {
        sound.volume = this.state.theVolume;
      });
    return (
      
      <div style={this.state.start} id="drum-machine">
        
        <div id="drums">
        <MusicPad
        clipVolume={this.state.theVolume}
        currentBank={this.state.currentBank}
        power={this.state.power}
        updateDisplay={this.displayClipName}
        ></MusicPad>
        </div>
        
        <div className='volume-slider'>
            <input
              id="slider"
              max='1'
              min='0'
              onChange={this.adjustVolume}
              step='0.01'
              type='range'
              value={this.state.theVolume}
            />
          </div>
        <div class="display" id="power-control" onClick={this.powerControl}>Power</div>
        <div class="display" id="bank-control" onClick={this.selectBank}>Change</div>
        <div class="display" id="display">{this.state.currentDisplay}</div>
        <div class="display" id="display-bank">{this.state.currentBankDisplay}</div>
        
      </div>
    )
  }
}
export default App;
